.contact-wrapper {
  display: flex;
  flex-direction: row;
  gap: 3rem;
  justify-content: space-between;
  padding-top: 11.7rem;
  padding-bottom: 12rem;

  @media  screen and (max-width: 450px) {
    flex-direction: column;
  }
}

.contact-typography {
  max-width: 43.4rem;
  h2 {
    font-size: 3.6rem;
    font-weight: 600;
    line-height: 4.3rem;
    letter-spacing: 0em;
    text-align: left;
    margin-bottom: 7rem;
  }

  p {
    font-size: 1.4rem;
    font-weight: 400;
    line-height: 2rem;
    letter-spacing: 0em;
    text-align: justified;
  }
}

input, textarea {
  width: 100%;
  height: 6rem;
  padding-left: 3rem;
  outline: none;
  border: 1px solid rgba(51, 51, 51, 1);
  border-radius: 6px;
}

.names {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 3rem;
}

.form-container {
  display: flex;
  flex-direction: column;
  width: 54rem;
  gap: 2.4rem;

  @media  screen and (max-width: 450px) {
    width: 100%;
  }

  .submit-btn {
    cursor: pointer;
    max-width: 16rem;
    max-height: 5rem;
    background: rgba(237, 76, 92, 1);
    border: none;
    font-size: 1.4rem;
    font-weight: 400;
    line-height: 1.7rem;
    letter-spacing: 0em;
    color: white;
    padding-left: 0;
    text-align: center;
  }
}