.focused-backgr {
  display: flex;
  background: rgba(237, 76, 92, 1);
}

.focused-wrapper {
  padding-top: 9.5rem;
  padding-bottom: 9.5rem;
  justify-content: space-between;
  gap: 5rem;

  @media  screen and (max-width: 450px) {
    flex-direction: column;
  }

  .img-container {
    max-width: 53.3rem;

    @media  screen and (max-width: 450px) {
      order: -1;
    }
  }
  img {
    width: 100%;
  }
}

.focused-typography {
  margin-top: 3rem;
  color: white;
  max-width: 44.5rem;

  h2 {
    font-size: 3.6rem;
    font-weight: 600;
    line-height: 4.3rem;
    letter-spacing: 0em;
    text-align: left;
    padding-bottom: 2.4rem;
  }

  p {
    font-size: 1.4rem;
    font-weight: 400;
    line-height: 2rem;
    letter-spacing: 0em;
    text-align: justified;
    margin-bottom: 4rem;
  }

  .focused-logos {
    max-width: 13.5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    img {
      width: 3.2rem;
    }
  }
}