header {
  width: 100vw;
  margin: 0 auto;
  background-image: linear-gradient(to right, rgba(#D52047, 0.8),
          rgba(#EE3D1B, 0.8)),
  url("../../assets/jpg/bg-hero.jpg");
  background-size: contain;

  @media  screen and (max-width: 800px) {
    height: 90vh;
  }

  .logo-container {
    max-width: 17.2rem;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      width: 100%;
    }
  }

  .burger-container {
    z-index: 999;
    display: none;

    @media  screen and (max-width: 500px) {
      display: flex;
      width: 6rem;
    }

    img {
      width: 100%;
    }
  }

  a {
    color: white;
    text-transform: uppercase;
    text-decoration: none;
  }
}

.navbar {

  display: flex;
  align-items: center;

  .menu-list {
    display: flex;
    flex-direction: row;
    font-size: 1.4rem;
    gap: 4rem;

    @media  screen and (max-width: 450px) {
      flex-direction: column;
      justify-content: center;
      font-size: 2rem;
      align-items: center;
      gap: 5rem;
      position: absolute;
      top: 0;
      right: -999px;
      background-color: #b02532;
      height: 70vh;
      width: 70vw;

      transition: .3s;
    }

    li {
      list-style: none;
    }
  }
}

.login-button {
  border: 2px white solid;
  border-radius: 6px;
  padding: 1rem 2rem;
  font-size: 1.4rem;
  text-align: center;


  @media  screen and (max-width: 450px) {
    max-width: 11rem;
    height: 5rem;
  }

}

.header-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-top: 5.5rem;

  @media  screen and (max-width: 450px) {
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }

}

.hero-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
  margin: 14rem auto 18.7rem;

  @media  screen and (max-width: 450px) {
    margin: 5rem auto 18.7rem;
  }

  h1 {
    color: white;
    text-transform: uppercase;
    font-size: 7.2rem;
    font-weight: 700;
    line-height: 86.4px;
    letter-spacing: 10px;
    
    animation-name: moveInLeft;
    animation-duration: 1s;
    animation-timing-function: ease-out;

    @media  screen and (max-width: 450px) {
      font-size: 4rem;
    }
  }

  p {
    font-size: 2.4rem;
    font-weight: 600;
    line-height: 36px;
    text-align: center;
    color: white;
    max-width: 54rem;

    animation-name: moveInRight;
    animation-duration: 1s;
    animation-timing-function: ease-out;

    @media  screen and (max-width: 450px) {
      font-size: 2rem;
    }
  }

  .hero-button {
    background-color: white;
    color: #ED4C5C;
    font-size: 1.4rem;
    font-weight: 600;
    line-height: 17px;
    letter-spacing: 0em;
    padding: 1.7rem 5.2rem;
    border-radius: 6px;
  }
}

.menu-list-hide {
  display: flex;
  flex-direction: row;
  font-size: 1.4rem;
  gap: 4rem;

  @media  screen and (max-width: 450px) {
    flex-direction: column;
    justify-content: center;
    font-size: 2rem;
    align-items: center;
    gap: 5rem;
    position: absolute;
    top: 0;
    right: 0;
    background-color: #b02532;
    height: 70vh;
    width: 70vw;

    transition: .3s;
  }

  li {
    list-style: none;
  }
}