footer {
  background: rgba(250, 250, 250, 1);
}

.footer-container {
  margin-top: 8.5rem;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 3rem;
  padding-bottom: 8rem;

  .footer-card {
    display: flex;
    flex-direction: column;
    li {
      text-decoration: none;
      list-style: none;
      font-size: 1.2rem;
      font-weight: 400;
      line-height: 1.9rem;
      letter-spacing: 0em;
      text-align: left;

    }

    h4 {
      font-size: 1.8rem;
      font-weight: 700;
      line-height: 2.2rem;
      letter-spacing: 0em;
      margin-bottom: 3.3rem;
    }

    p {
      font-size: 1.2rem;
      font-weight: 400;
      line-height: 1.8rem;
      letter-spacing: 0em;
      text-align: left;
      padding-bottom: 1rem;
    }
  }
}

.touch-logos {
  display: flex;
  max-width: 6.6rem;
  justify-content: space-between;
}

.support-logo {
  max-width: 21.6rem;

  @media  screen and (max-width: 1025px) {
    max-width: 18rem;
  }

  img {
    width: 100%;
  }
}

