.background-gray {
  background: #FAFAFA;
}

.features-wrapper {
  flex-direction: column;

  h2 {
    text-align: center;
    margin-top: 6.3rem;
    margin-bottom: 6.3rem;
  }
}

.features-slides {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 2rem;
  margin-bottom: 20rem;

  @media  screen and (max-width: 450px) {
    flex-direction: column;
    align-items: center;
    gap: 19rem;
  }

  .features-slide {
    display: flex;
    justify-content: center;
    position: relative;
    max-width: 35rem;
    max-height: 25rem;
    border-radius: 8px;

    img {
      width: 100%;
    }

    h3 {
      font-size: 1.8rem;
      font-weight: 700;
      line-height: 2.2rem;
      letter-spacing: 0em;
      text-align: center;
      margin-bottom: 2rem;
    }

    p {
      margin-bottom: 2.5rem;
      font-size: 1.5rem;
      font-weight: 400;
      line-height: 1.8rem;
      letter-spacing: 0em;
      text-align: center;
    }
  }

  .features-slide-typography {
    padding: 9% 10% 5%;
    background: white;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 29.3rem;
    margin: 0 3rem;
    border-radius: 8px;
    top: 65%;
    box-shadow: 0px 5px 30px 0px #0000001A;
  }
}