.wrapper-tech {
  flex-direction: column;
}

.technology-cards {
  display: flex;
  flex-direction: row;

  .card-number {
    display: flex;
    position: relative;
    p {
      font-size: 10rem;
      font-weight: 700;
      line-height: 120px;
      letter-spacing: 0em;
      text-align: center;
      color: rgba(16, 16, 16, 0.08);
    }
  }

  .technology-card {
    &-desc {
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: 19.1rem;

      img {
        height: 8rem;
      }
    }
  }
}

