.management-wrapper {
  padding-top: 9.5rem;
  padding-bottom: 9.5rem;
  justify-content: space-between;

  @media  screen and (max-width: 800px) {
    gap: 3rem;
  }

  @media  screen and (max-width: 450px) {
    flex-direction: column;
  }


  .img-container {
    max-width: 44.5rem;

    @media  screen and (max-width: 450px) {
      order: -1;
    }
  }
  img {
    max-width: 100%;
  }
}

.management-typography {
  max-width: 44.5rem;
  margin-top: 4rem;
  p {
    font-size: 1.4rem;
    font-weight: 400;
    line-height: 2rem;
    letter-spacing: 0em;
    margin-bottom: 2.5rem;
  }
}